.modal-content p,
    .modal-content .title {
    font-family: 'Open Sans';
}
.modal-content .title {
    clear: both;
    *zoom: 1;
    font-size: 24px;
    font-weight: bold;
    color: $black;
    margin-bottom: 30px;
}
.modal-content .title:before {
    content: '';
    display: table;
}
.modal-content .title:after {
    content: '';
    display: table;
    clear: both;
}
.modal-content .title::before,
    .modal-content .title::after {
    content: '';
    clear: both;
    display: block;
}
.modal-content .text {
    float: left;
    -webkit-box-ordinal-group: 2;
    -webkit-order: 1;
        -ms-flex-order: 1;
            order: 1;
    margin-bottom: 15px;
}
@media (min-width: $(desktop)) {
    .modal-content .text {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 7/12 - (30px - 30px * 7/12));
    }
    .modal-content .text:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .modal-content .text:last-child {
        margin-right: 0;
    }
    .modal-content .text:nth-child(12 '' ''n) {
        margin-right: 0;
        margin-left: auto;
    }
}
.modal-content .text p {
    font-size: 14px;
    line-height: normal;
}
.modal-content .image {
    -webkit-box-ordinal-group: 3;
    -webkit-order: 2;
        -ms-flex-order: 2;
            order: 2;
    float: right;
    margin-right: 0;
}
@media (min-width: $(desktop)) {
    .modal-content .image {
        -webkit-box-flex: 0;
        -webkit-flex: 0 0 auto;
            -ms-flex: 0 0 auto;
                flex: 0 0 auto;
        width: calc(99.99% * 5/12 - (30px - 30px * 5/12));
    }
    .modal-content .image:nth-child(1n) {
        margin-right: 30px;
        margin-left: 0;
    }
    .modal-content .image:last-child {
        margin-right: 0;
    }
    .modal-content .image:nth-child(12 '' ''n) {
        margin-right: 0;
        margin-left: auto;
    }
}
.modal-content .image img {
    width: 100%;
